.scene {
  width: 100%;
  height: 100%;
  min-height: 100vh;
  background: $first-screen-bg;

  &::before {
    content: '';
    background-image: url("../picture/ton.png");
    background-repeat: no-repeat;
    background-size: cover;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 1;
  }

  &__element {
    position: absolute;
    width: 100%;
    height: 100%;

    &--moon {
      z-index: 3;
    }

    &--ice {
      z-index: 2;
    }
  }

  &__moon {
    position: absolute;
    right: 9%;
    top: 8%;
  }

  &__clouds {
    position: absolute;
    top: 3%;
    left: 1%;
    width: 100%;

    svg {
      width: 100%;
      height: auto;
      path {
        &:nth-child(1) {
         animation: cloudsMover 16s infinite linear;
        }

        &:nth-child(2) {
          animation: cloudsMover 12s infinite linear;
        }

        &:nth-child(3) {
          animation: cloudsMover 10s infinite linear;
        }

        &:nth-child(4) {
          animation: cloudsMover 12s infinite linear;
        }
      }
    }
  }

  &__north-shine {
    position: absolute;
    top: -1%;
    left: 1%;
    width: 101%;
  }

  &__stars {
    position: absolute;
    left: -7.4%;
    top: 0;
    width: calc(100% + 7.4%);
    svg{
      width: 100%;
      height: auto;
      path{
        animation: starsFade 4s ease infinite;
        &:nth-child(2n){
          animation-delay: 1s ;
        }
        &:nth-child(3n){
          animation-delay: 3s ;
        }
        &:nth-child(4n){
          animation-delay: 5s ;
        }
      }
    }
  }

  &__ice {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    z-index: 2;

    &__element {
      position: absolute;
      width: fit-content;
      height: fit-content;
      z-index: 0;
    }

    @for $i from 1 through 23 {
      &-#{$i} {
        animation: iceMover (random(10) + 3s) infinite linear;
      }
    }

    &-0 {
      left: 1%;
      bottom: 30%;
    }

    &-1 {
      left: -1%;
      bottom: 53%;
    }

    &-2 {
      right: 33.5%;
      bottom: 59%;
    }

    &-3 {
      right: 32.5%;
      bottom: 8%;
    }

    &-4 {
      right: 0.5%;
      bottom: 24%;
      z-index: 1;
    }

    &-5 {
      right: 0.5%;
      bottom: 7%;
    }

    &-6 {
      right: 3.5%;
      bottom: -23%;
    }

    &-7 {
      right: 14%;
      bottom: 12%;
    }

    &-8 {
      right: 17.5%;
      bottom: 26%;
      z-index: 1;
    }

    &-9 {
      right: 26.5%;
      bottom: -6%;
    }

    &-10 {
      right: 37.5%;
      bottom: -26%;
    }

    &-11 {
      bottom: 58%;
      left: 45%;
    }

    &-12 {
      bottom: 73%;
      left: 40.9%;
    }

    &-13 {
      bottom: 25%;
      left: 31%;
    }

    &-14 {
      bottom: 41%;
      left: 25%;
    }

    &-15 {
      bottom: 3%;
      left: 34.1%;
    }

    &-16 {
      bottom: 1%;
      left: 27.1%
    }

    &-17 {
      bottom: -16%;
      left: 16.1%;
    }

    &-18 {
      bottom: 17%;
      left: 15%;
    }

    &-19 {
      bottom: -8%;
      left: -1.5%;
    }

    &-20 {
      bottom: 64%;
      left: 9.1%;
    }

    &-21 {
      bottom: -23%;
      left: 39.5%;
    }

    &-22 {
      bottom: 41%;
      left: 52%;
    }

    &-23 {
      left: 6.3%;
      bottom: 35%;
    }
  }

  &__rocks {
    position: absolute;
    bottom: 14%;
    left: -13.51%;
    width: 113.51%;
    svg{
      width: 100%;
      height:auto;
    }
  }

  &__ship {
    position: absolute;
    bottom: 23px;
    right: -9%;
    z-index: 1;
    animation: mover 5s 0.1s infinite linear;
  }

  &__watter {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;

    &__main {
      width: 120%;
      margin-left: -9%;
      display: flex;
      height: auto;
      position: absolute;
      top: 0;
      z-index: 0;
      svg{
        width: 100%;
        height: auto;
      }
    }

    &__img {
      position: relative;
      z-index: 1;
      width: 118%;
      height: auto;
      margin-left: -9%;
    }
  }
}