//Default font values:
// font-size: 142px
h1, .h1 {
  font-size: clamp(3.25rem, 8.451vw + 1.269rem, 8.875rem);
  line-height: clamp(3.25rem, 5.784vw + 1.894rem, 7.1rem);
  font-style: normal;
  font-weight: 400;
  text-align: center;
  text-transform: uppercase;
  margin: 15px 0 15px 0;
}

// font-size: 100px
h2, .h2 {
  font-style: normal;
  font-size: clamp(3rem, 4.883vw + 1.856rem, 6.25rem);
  line-height: clamp(3rem, 4.883vw + 1.856rem, 6.25rem);
  font-weight: 300;
  margin: 15px 0 15px 0;
  text-align: center;
}
// font-size: 50px
h3, .h3 {
  font-style: normal;
  font-size: clamp(2.375rem, 1.127vw + 2.111rem, 3.125rem);
  line-height: clamp(2.375rem, 1.127vw + 2.111rem, 3.125rem);
  font-weight: 400;
  margin: 15px 0 15px 0;
}
// font-size: 40px
h4, .h4 {
  font-style: normal;
  font-size: clamp(2rem, 0.751vw + 1.824rem, 2.5rem);
  line-height: clamp(2.5rem, 0.939vw + 2.28rem, 3.125rem);
  font-weight: 400;
  margin: 15px 0 15px 0;
}
// font-size: 35px
h5, .h5 {
  font-style: normal;
  font-weight: 400;
  font-size: clamp(1.25rem, 1.408vw + 0.92rem, 2.188rem);
  line-height: clamp(1.25rem, 1.408vw + 0.92rem, 2.188rem);
  margin: 7px 0 7px 0;

  &.small {
    // font-size: 30px
    font-size: clamp(1.125rem, 1.127vw + 0.861rem, 1.875rem);
    line-height: clamp(1.35rem, 1.352vw + 1.033rem, 2.25rem);
    letter-spacing: -0.02em;
  }
}
// font-size: 20px
h6, .h6 {
  font-style: normal;
  font-weight: 500;
  font-size: clamp(1rem, 0.376vw + 0.912rem, 1.25rem);
  line-height: clamp(1.5rem, 0.563vw + 1.368rem, 1.875rem);
  margin: 7px 0 7px 0;
  letter-spacing: -0.02em;
}
// font-size: 20px
p, .p {
  font-style: normal;
  font-size: clamp(1rem, 0.376vw + 0.912rem, 1.25rem);
  line-height: clamp(2rem, 0.751vw + 1.824rem, 2.5rem);
  font-weight: 400;
  margin: 15px 0 15px 0;
}

.small {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 30px;
  text-align: center;
}

.text-center {
  text-align: center;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;

  li {
    display: flex;
  }
}

a {
  text-decoration: none;
  transition: color 0.4s ease-out;
  display: inline-flex;
  color: inherit;
}


//Extra classes:

.fw_400 {
  font-weight: 400;
}

.fw_500 {
  font-weight: 500;
}

.fw_600 {
  font-weight: 600;
}

.fw_700 {
  font-weight: 700;
}

.fw_800 {
  font-weight: 800;
}

.caps {
  text-transform: uppercase;
  letter-spacing: 0.5em;
}

.uncaps {
  text-transform: none;
  letter-spacing: 0;
}

.italic {
  font-style: italic;
}

.ff-additional {
  font-family: $additional-font;
}

.ff-main {
  font-family: $main-font;
}


.small {
  font-size: 14px;
}

.medium-size-text {
  font-size: 16px;
}

.ls_none {
  letter-spacing: 0;
}

@media screen and (max-width: $breakpoint-sm) {

  .small {
    line-height: 1.6;
  }
}

